.container {
  display: flex;
  max-width: 1000px;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  flex-direction: column;
}

.body {
  padding: 24px 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.contact_us {
  display: flex;
  max-width: 1000px;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  flex-direction: column;
  margin-top: 48px;
}

.content {
  text-align: justify;
}

.divider {
  height: 48px;
}

.small_divider {
  height: 24px;
}

.whatsapp {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.linkedin {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.mail {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.whatsapp_icon {
  margin-right: 6px;
  color: #25d366;
}
.mail_icon {
  margin-right: 6px;
  color: rgba(0, 0, 0, 0.8);
}

.linkedin_icon {
  margin-right: 6px;
  color: rgb(0, 119, 181);
}

a {
  text-decoration: none;
  color: rgb(25, 118, 210);
}
