.container {
  display: flex;
  max-width: 1000px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 24px;
}
.right {
  min-width: 120px;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.left {
  display: flex;
  flex-direction: column;
}

.name {
  font-weight: 600;
  color: rgba(0, 0, 0, 0.85);
  font-size: 36px;
  text-transform: uppercase;
  font-family: "Baskervville";
  font-weight: 400;
}

.designation {
  font-weight: 600;
  color: rgba(0, 0, 0, 0.65);
  font-size: 24px;
  font-family: "Baskervville";
  font-weight: 400;
}

@media (max-width: 768px) {
  .name {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.85);
    font-size: 24px;
    text-transform: uppercase;
  }

  .designation {
    font-weight: 600;
    color: rgba(0, 0, 0, 0.65);
    font-size: 18px;
  }

  .right {
    margin-left: 8px;
  }
}
